.welcome{
    padding: 0;
}
.welcome__video{
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    overflow: hidden;
    z-index: -1;
    > img,
    > video{
        min-width: 100%;
        min-height: 100vh;
    }
}
.welcome__container{
    color: #fff;
    padding: 150px 0 50px;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.welcome__messages-title{
    margin-bottom: 0.6em;
    color: inherit;
}
.welcome__messages-subtitle{
    margin-bottom: 0.6em;
    color: inherit;
}
.welcome__messages-brief{
    line-height: 1.7;
    margin-bottom: 2em;
}

// cards
.welcome__card{
    height: 135px;
    .card__image{
        flex: 0 0 47%;
    }
}

